@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500&display=swap);
html{background-color:#000000}body{background:url(/static/media/home-bg.85a4a624.jpg) repeat-y center center;background-size:cover;display:flex;flex-direction:column;height:100%}#cont{max-width:1200px;width:100%;padding-left:0;padding-right:0}#menu{background-color:#1d1d1d;min-width:100%;display:flex;justify-content:space-evenly;float:right}#navbar-toggler{border:none;color:#1d1d1d}.Navbar{background:#1d1d1d;margin-top:28px;margin-bottom:34px;height:90px;padding-bottom:15px;line-height:40px}#nav-link{color:#fff;font-weight:600;text-decoration:none;padding-left:15px}#nav-link:hover{color:#474646}.wrapper{max-width:1100px;justify-content:space-evenly;height:100%;position:relative;align-content:center;display:block;margin-top:205px;margin-bottom:2%;margin-left:auto;margin-right:auto}.logo{max-width:30%;float:left;margin-right:2%}.img{max-width:50%;float:left;padding:10px}.home-text{float:left;position:relative;font-family:"Poppins";font-style:normal;font-weight:300;font-size:14px;line-height:30px;min-height:150px;color:#ffffff;background-color:rgba(0,0,0,0.3);max-width:65%;padding:2%}.card-quote{color:#ffffff;font-family:"Poppins";font-style:italic;font-weight:200;font-size:12px;line-height:20px;text-align:right;justify-content:flex-end;margin-top:5px;margin-right:10px;margin-left:40px;margin-bottom:0px}.card-txt{color:#ffffff;line-height:25px;line-height:20px}.card-name{font-size:18px;line-height:40px}.footer{background-color:#161616;flex:0 0 auto;color:#fff}

